import { useEffect, useState } from "react";

export const UseEffectHook = () => {
   const [count, setCount] = useState(0);

   useEffect(() => alert(` ${count}`))

   return (
      <div>
         <p>  {count} </p>
         <button className="btn btn-primary mb-3" onClick={() => setCount(count + 1)}></button>
      </div>
   )
}